/* Login.css */
.container {
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%; /* Set the width to 100% of the viewport */
  margin: 0 auto; /* Center the container horizontally */
  box-sizing: border-box; /* Include padding and border in the width */
}

h2 {
  text-align: center;
}

.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  margin-bottom: 5px;
}

input {
  padding: 8px;
  margin-bottom: 10px;
  width: 100%; /* Set the input width to 100% */
  box-sizing: border-box; /* Include padding in the input width */
}

button {
  padding: 8px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.link-btn {
  color: #007bff;
  text-decoration: none;
  margin-left: 5px;
}

.register-text {
  margin-top: 10px;
  text-align: center;
}
